/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #83aaa7;
  color: #0a0500;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #0a0500;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #0a0500;
  font-size: 2rem;
}

.page-name {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.page-description {
  text-align: center;
}

.app-container {
  /* Hide scrollbar for webkit-based browsers */
}
@media (min-width: 768px) {
  .app-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.app-container .image {
  height: 20rem;
  background-image: url("../img/main.png");
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .app-container .image {
    height: auto;
  }
}
.app-container .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .app-container .app {
    overflow-y: scroll;
  }
}
.app-container .bar {
  margin-bottom: 5rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .app-container .bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.app-container .bar .button {
  background-color: #83aaa7;
  padding: 1.5rem 4rem;
  color: #0a0500;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .app-container .bar .button {
    width: auto;
  }
}
.app-container .bar .button:hover {
  background-color: #669491;
  cursor: pointer;
}
.app-container .bar span {
  font-weight: 700;
  color: #d0561a;
}
.app-container ::-webkit-scrollbar {
  display: none;
}

.section {
  padding: 5rem 0;
  display: none;
}
.section .text-center {
  text-align: center;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.activities-list {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .activities-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.activities-list .activity {
  align-items: center;
  background-color: #d3b536;
  padding: 2rem;
  border-radius: 1rem;
  transition: transform 300ms;
  text-align: center;
}
.activities-list .activity:hover {
  transform: scale(1.1);
}
.activities-list .activity p {
  margin: 0 0 1.25rem 0;
  color: #0a0500;
}
.activities-list .activity .activity-price {
  color: #d0561a;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.activities-list .activity .activity-name {
  color: #0a0500;
  font-weight: 700;
  font-size: 2rem;
  margin: 0;
}
.activities-list .activity .activity-description {
  color: #0a0500;
  font-weight: 400;
  font-size: 1.5rem;
  margin-top: 0.5rem;
}
.activities-list .activity.selected {
  background-color: #8fa226;
}
.activities-list .activity.selected p {
  color: #0a0500;
}
.activities-list .activity.selected .activity-price {
  color: #d0561a;
}

.summary-content p {
  margin-bottom: 1rem;
}
.summary-content span {
  color: #d0561a;
  font-weight: 700;
  font-size: 2.4rem;
}
.summary-content .total {
  text-align: right;
  margin-bottom: 5rem;
}

.activity-container {
  padding: 3rem 0;
  border-bottom: 3px solid #8fa226;
}
.activity-container:last-of-type {
  margin-bottom: 5rem;
}

.reservations {
  list-style: none;
  padding: 0;
  text-decoration: none;
}
.reservations li {
  padding: 1rem;
  border-top: 3px solid #8fa226;
}
.reservations li p {
  font-weight: 700;
  color: #d0561a;
}
.reservations li span, .reservations li .activity {
  font-weight: 400;
  color: #0a0500;
}

.form {
  margin-top: 5rem;
  width: 100%;
}

.field {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.field label {
  flex: 0 0 10rem;
}
.field input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
  background-color: #d3b536;
}
.field input:disabled {
  background-color: #d0561a;
  color: #0a0500;
  cursor: not-allowed;
}

.button {
  background-color: #8fa226;
  padding: 1.5rem 4rem;
  color: #0a0500;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .button {
    width: auto;
  }
}
.button:hover {
  background-color: #6b791c;
  cursor: pointer;
}

.actions {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .actions {
    display: flex;
    justify-content: space-between;
  }
}
.actions a {
  color: #0a0500;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alert {
  padding: 1.25rem;
  color: #0a0500;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alert.error {
  background-color: #FFF82E;
  color: #0a0500;
}
.alert.success {
  background-color: #3bb105;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #d0561a;
  border: none;
  color: #0a0500;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #d0561a;
}
.tabs button.active {
  background-color: #8fa226;
  color: #0a0500;
}

.pagination {
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .pagination {
    display: flex;
    justify-content: space-between;
  }
}
.pagination button {
  margin: 0 0 1rem 0;
}

.hide {
  visibility: hidden;
}